<template>
  <div class="row">
    <div class="col-12" v-loading="updatingStatus">
      <StreamBarcodeReader
        @decode="(a, b, c) => onDecode(a, b, c)"
        @loaded="() => onLoaded()"
      ></StreamBarcodeReader>
    </div>
    <p class="text-center">Input Value: {{ text || "Nothing" }}</p>
    <audio id="found">
      <source src="media/Barcode.mp3" type="audio/wav" />
      Your browser does not support the audio element.
    </audio>
  </div>
</template>

<script>
import { StreamBarcodeReader } from "vue-barcode-reader";

export default {
  name: "HelloWorld",
  components: {
    StreamBarcodeReader,
  },
  data() {
    return {
      text: "",
      id: null,
      found: null,
      updatingStatus: false,
    };
  },
  props: {},
  watch: {
    text() {
      this.update();
    },
  },
  mounted() {
    this.$store.commit("setIsReady", true);
  },
  methods: {
    onDecode(a, b, c) {
      console.log(a, b, c);
      this.text = a;
      this.playSound("found");
    },
    update() {
      this.updatingStatus = true;
      this.$axios
        .put("update-sale-product/", {
          barcode: this.text,
        })
        .then(() => {
          this.updatingStatus = false;
          this.$noti.display({
            title: "Success",
            color: "success",
            message: "Cool! Sale has been successfully updated.",
          });
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
          this.updatingStatus = false;
        });
    },
    playSound(id) {
      document.getElementById(id).play();
    },
    onLoaded() {
      console.log("load");
    },
  },
};
</script>
<style scoped>
</style>
